    font-size: 1.3rem;

}
.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}



.texto-testimonio{
    
    font-size: 1.1rem;
    text-align: justify;

}


















